<template>
	<div class="_page flex-xc-yc">
		<div id="ggl-body">
			<div
				class="rukou"
				@click="switchType('')"
			>
				<h2><span>刮刮乐代码</span>查询工具</h2>
			</div>
			<div :class="`typeSel flex-xb ${isActive ? 'on' : ''}`">
				<div
					:class="`ev ${type == 'ti' ? 'on' : ''}`"
					@click="switchType('ti')"
				>
					<div class="pic">
						<img
							:src="require('@/assets/img/caiPiao/cp1-2.png')"
							alt="体彩"
						/>
					</div>
				</div>
				<div
					:class="`ev ${type == 'fu' ? 'on' : ''}`"
					@click="switchType('fu')"
				>
					<div class="pic">
						<img
							:src="require('@/assets/img/caiPiao/cp1-3.png')"
							alt="福彩"
						/>
					</div>
				</div>
			</div>

			<div :class="`control flex-xb ${isActive ? 'on' : ''}`">
				<el-input
					v-model="input"
					placeholder="请输入彩票代码中的任意一个字母"
					@input="search"
				></el-input>

				<ul>
					<li
						v-for="(item, i) in list"
						:key="i"
					>
						中奖代码：<span>{{ item.code }}</span>
						<i />
						中奖金额：<span>{{ item.money }}</span
						>元
					</li>
					<li v-if="input != ''">注:大奖无固定字母组合</li>
				</ul>
			</div>

			<div class="rukou">
				<h5>author：木瓜</h5>
				<p>此工具仅做交流使用，如有侵权，请联系删除</p>
			</div>
		</div>
	</div>
</template>

<script>
import '@/assets/css/caiPiao.css';
export default {
	name: 'caiPiao',
	data() {
		return {
			isActive: false,
			type: null,
			input: '',
			list: [],
			tiLi: [
				{ money: '5', code: 'BRZ' },
				{ money: '10', code: 'CVK' },
				{ money: '15', code: 'THL' },
				{ money: '20', code: 'EFQ' },
				{ money: '25', code: 'MOC' },
				{ money: '30', code: 'EUL' },
				{ money: '50', code: 'IGK' },
				{ money: '55', code: 'MAR' },
				{ money: '60', code: 'LSV' },
				{ money: '80', code: 'ZDJ' },
				{ money: '100', code: 'MFR' },
				{ money: '150', code: 'YSO' },
				{ money: '180', code: 'PRO' },
				{ money: '200', code: 'NJS' },
				{ money: '260', code: 'BCN' },
				{ money: '300', code: 'NJI' },
				{ money: '400', code: 'ENX' },
				{ money: '450', code: 'XYG' },
				{ money: '500', code: 'UHQ' },
				{ money: '560', code: 'HAN' },
				{ money: '600', code: 'MOE' },
				{ money: '800', code: 'UHA' },
				{ money: '900', code: 'HIV' },
				{ money: '1000', code: 'YAO' },
				{ money: '1350', code: 'GAR' },
				{ money: '1500', code: 'PAV' },
				{ money: '1600', code: 'HEC' },
				{ money: '2000', code: 'MTK' },
				{ money: '3000', code: 'TBL' },
				{ money: '4000', code: 'ZME' },
				{ money: '5000', code: 'N以后的任意字母' },
			],
			fuLi: [
				{ money: '2', code: 'HYQ' },
				{ money: '5', code: 'CTW' },
				{ money: '10', code: 'KSX' },
				{ money: '15', code: 'DPT' },
				{ money: '20', code: 'ARS' },
				{ money: '25', code: 'DKO' },
				{ money: '30', code: 'BHJ' },
				{ money: '35', code: 'CMN' },
				{ money: '40', code: 'CPS' },
				{ money: '45', code: 'GIN' },
				{ money: '50', code: 'CYZ' },
				{ money: '60', code: 'DRV' },
				{ money: '70', code: 'FQW' },
				{ money: '80', code: 'CLO' },
				{ money: '90', code: 'ANO' },
				{ money: '100', code: 'ERW' },
				{ money: '150', code: 'EIL' },
				{ money: '180', code: 'JPZ' },
				{ money: '200', code: 'ALM' },
				{ money: '300', code: 'BQS' },
				{ money: '400', code: 'AFG' },
				{ money: '450', code: 'GKL' },
				{ money: '500', code: 'DSW' },
				{ money: '800', code: 'GPW' },
				{ money: '900', code: 'AJK' },
				{ money: '1000', code: 'DQU' },
				{ money: '1500', code: 'EJO' },
				{ money: '2000', code: 'BTV' },
				{ money: '2000+', code: '以上为任意字母组合' },
			],
		};
	},
	created() {},
	methods: {
		switchType(type) {
			if (type == '') {
				this.isActive = false;
				this.list = [];
				this.type = type;
				return;
			}
			this.isActive = true;
			this.type = type;
			this.list = [];

			if (this.input != '') {
				this.search();
			}
		},
		search() {
			let searchList = [];
			let keywords = this.input.toUpperCase();
			if (this.type == 'ti') {
				searchList = this.tiLi;
			}
			if (this.type == 'fu') {
				searchList = this.fuLi;
			}

			this.list = searchList.filter((x) => {
				if (x.code.indexOf(keywords) > -1) {
					return x;
				}
			});
		},
	},
};
</script>

